import React, { Component } from 'react';
import {Redirect, Route, Switch} from 'react-router-dom'
import {
    Form,
    Icon,
    Input,
    Button,
    message
} from 'antd'

import Home from "../home/home";
import Write from "../home/write/wirte";
import Details from "../home/details/details";
import SonExampleComment from '../home/details/datails_son/datails_son'

import {adminUser} from '../../ajax/types'
import storageUtils from '../../utils/storageUtils'
import User from '../../utils/users'
import './admin.less'

const Item =Form.Item

class Admin extends Component {

  state = {
    tologin:false,
    tohome:false
  }

  toLogin = () =>{
    //this.props.history.push('/login')
    this.setState({tologin:true})

  }

  componentDidMount() {
    storageUtils.removeUser()
    storageUtils.removeWrite1()
    storageUtils.removeWrite2()
    User.user = {}
  }

  handleSubmit = (event)=>{
    event.preventDefault() //阻止表单的默认行为
    // 对所有表单字段进行检验
    this.props.form.validateFields(async(err, values) => {
      // 检验成功
      if (values.userName && values.password) {
        // 请求登陆
        const result = await adminUser(values.userName, values.password)
        if (result.flag === 1) { // 登陆成功
          // 提示登陆成功
          console.log(result)
          message.success('登陆成功')
          const user = result.userinfo // 保存在内存中
          storageUtils.saveUser(user) // 保存到local中
          User.user = user
          // 跳转到管理界面 (不需要再回退回到登陆)
          this.setState({tohome:true})
        } else {
          message.error('账户或密码不正确')
        }
      } else {
        message.error('用户名和密码不能为空')
      }
    });
  }

    render() {
        const form = this.props.form
        const {getFieldDecorator} =form
        const {tologin,tohome} = this.state
        if(tologin){
          return <Redirect to='/login'/>
        }
        if(tohome){
          return <Redirect to='/home'/>
        }
        return (
          <div className="admin">
              <header className="admin-header">
                  <h1>BBS</h1>
              </header>
              <section className="admin-content">
                  <h2>用户登陆</h2>
                  <Form onSubmit={this.handleSubmit} className="admin-form">
                    <Item>
                      {
                        getFieldDecorator('userName', {
                        })(
                          <Input
                            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="用户名"
                          />
                        )
                      }
                    </Item>
                    <Item>
                      {
                        getFieldDecorator('password', {

                        })(
                          <Input
                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            type="password"
                            placeholder="密码"
                          />
                        )
                      }
                    </Item>
                      <Item>
                          <Button type="primary" htmlType="submit" className="admin-form-button">
                              登录
                          </Button>
                      </Item>
                    <Item>
                      <Button type="primary" onClick={this.toLogin} className="admin-form-button">
                        注册
                      </Button>
                    </Item>
                  </Form>
              </section>
          </div>
        )
    }
}

const WrapAdmin = Form.create()(Admin)
export default WrapAdmin
