import qs from 'qs'

import axios from 'axios'
import {message} from 'antd'



export default function ajax(url,data={},method='GET',deFault=true) {

    if(deFault){
      data = qs.stringify(data)
    }
    return new Promise((resolve,reject)=>{
        let promise
        if(method==='GET'){
          promise = axios.get(url,{
            params:data
          })
        }else{
          promise = axios.post(url,data,
            {
                setRequestProperty:("Content-Type","application/x-javascript;charset=utf-8")//发送json数据
              })
        }
        promise.then(response =>{
          console.log(response)
          resolve(response.data)
        }).catch(error =>{
          message.error("请求错误")
        })
      })
}



