import React, { Component } from 'react';
import PubSub from 'pubsub-js'

import SonExampleComment from './datails_son/datails_son'
import {getSonDetails} from "../../../ajax/types"
import StorageUtils from '../../../utils/storageUtils'
import './details.less'

export default class Datails extends Component {

    state = {
      detailsReload:false,  //刷新
      display1:'none',    //能否删除留言
      detail_id:0,        //文章id
      detail_mid:0,       //用户id
      detail:{ui:{id:0,userName:""},mtime:'',mcontent:'',title:''},
      comments:[
      ],
      YorN:'none',
      content: '',
      comment:'',
    }

  componentWillUnmount() {
    // 卸载异步操作设置状态
    clearTimeout(this.timeouter)
    this.setState = (state, callback) => {
      return
    }
  }
  //获取评论和对应的子评论
  async componentDidMount (){
    StorageUtils.saveWrite1('none')
    StorageUtils.saveWrite2('block')
    const author = this.props.location.state.record   //组件间的属性传递
    PubSub.publish('record',author)
    var detail_id = author.id                     //文章id
    var detail_mid = author.uid                   //作者id
    if(StorageUtils.getUser().id===detail_mid){
      this.setState({display1:'block'})
    }
    var comments = await getSonDetails(detail_id)
    console.log(comments)
    if(comments.length>0){
      const content = comments[0].mcontent
      this.setState({content})
    }
    if(comments.length>1){
      comments = comments.slice(1,comments.length).reverse()
    }else{
      comments = []
    }
    this.reload = PubSub.subscribe('reload1',async (msg,data)=>{
      if(data === 'true'){
        this.setState({comments:[]})
        var comments = await getSonDetails(detail_id)
        comments = comments.slice(1,comments.length).reverse()
        this.setState({detail:author,detail_id,detail_mid,comments:comments})
      }
    })
    this.setState({detail:author,detail_id,detail_mid,comments})
}

    render() {
      const {comments,detail,content,display1} = this.state
      let display = comments.length > 0 ? 'none' : 'block'
      //子评论
        return (
          <div className="detail">
            <div className="detail-father">
              <p className='detail-title'>{detail.title}</p>
              <p className='author'>{detail.ui.userName}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{detail.mtime}</p>
              <div className='comment'>
                <p >{content}</p>
              </div>
            </div>
            <h1>评论：</h1>
            <div className="detail-sons">
              <h2 style={{ display: display }} className='h2'>暂无评论，在左方添加评论！！！</h2>
              <div className="detail-son">
                {
                   comments.map((comment, index) => {
                      return <SonExampleComment comment={comment} key={index} mid={comment.mid} display1={display1}/>
                  })
                }
              </div>
            </div>
          </div>
        );
    }
}
