import ajax from './ajax'
//定义请求的方法
const Base = ''
//注册
export const addNewUser = (userName,password) =>ajax(Base+'/admininfo/register',{userName,password},'POST',true)
//登录
export const adminUser = (userName,password) =>ajax(Base+'/admininfo/login',{userName,password},'POST')
//发表自己的留言
export const addComment = (infos) =>ajax(Base+'/messageinfo/commitmessage',infos,'POST',false)
//发表评论
export const addComments = (messagedetail) =>ajax(Base+'/messageinfo/writecomment',messagedetail,'POST')
//获取标题
export const getTitles = (page, rows,desc) =>ajax(Base+'/messageinfo/list',{page,rows,desc},'POST')
//获取细目
export const getDetail = (mid) =>ajax(Base+'/messageinfo/getmessageinfo',{mid},'POST')
//获取子评论
export const getSonDetails = (mid) =>ajax(Base+'/messageinfo/getmessagedetail',{mid},'POST')
//删除留言
export const delComment = (mids) =>ajax(Base+'/messageinfo/deletemessage',{mids},'POST')




