import React, { Component } from 'react';
import PropTypes from 'prop-types'
import './details_son.less'

export default class SonExampleComment extends Component {

    state = {
        mid : 0,
        comment :{},

    }
    componentWillUnmount() {
        this.setState({load:false})
        // 卸载异步操作设置状态
        clearTimeout(this.timeouter)
        this.setState = (state, callback) => {
            return
        }
    }
    async componentDidMount() {
        const mid = this.props.mid
        const comment = this.props.comment
        this.setState({mid,comment})
    }

    render() {
        const {comment} = this.state
        return (
        <div className='detail-son'>
            <p className='author'>匿名&nbsp;&nbsp;:</p>
            <div className='comment'>
                {comment.mcontent}
            </div>
        </div>
        )
    }
}
SonExampleComment.propTypes = {
    comment: PropTypes.object.isRequired,
    mid: PropTypes.number.isRequired,
    display1:PropTypes.string.isRequired
}

