import React, { Component } from 'react';
import {Input,Button,message} from 'antd';
import PubSub from 'pubsub-js'

import './wirte.less'
import {addComment,addComments} from '../../../ajax/types'
import storageUtils from '../../../utils/storageUtils'

const { TextArea } = Input;

export default class Write extends Component {
    state = {
        contentCounts:200,
        commentCounts:100,
        title:"",
        content:"",
        comment:"",
        record:{}
    }

  componentWillUnmount() {
    // 卸载异步操作设置状态
    clearTimeout(this.timeouter)
    this.setState = (state, callback) => {
      return
    }
  }
    //获取时间
    getDate = (Date)=>{
      var time = Date.getFullYear()+"-"
      if(Date.getMonth()>9)
        time = time+Date.getMonth()+"-"
      else
        time = time+"0"+Date.getMonth()+"-"
      if(Date.getDate()>9)
        time = time+Date.getDate()+"  "
      else
        time = time+"0"+Date.getDate()+"  "+Date.getHours()+":"+Date.getMinutes()+":"
      if(Date.getHours()>9)
        time = time+Date.getHours()+":"
      else
        time = time+"0"+Date.getHours()+":"
      if(Date.getMinutes()>9)
        time = time + Date.getMinutes() + ":"
      else
        time = time + "0" + Date.getMinutes() + ":"
      if(Date.getSeconds()>9)
        return time+Date.getSeconds()
      else
        return time+"0"+Date.getSeconds()
    }

    //退出
    out = ()=>{
      if(window.confirm('确定退出登录吗？')){
        storageUtils.removeUser()
        this.props.history.replace('/')
      }
    }

    componentDidMount() {
      this.record = PubSub.subscribe('record',(msg,data)=>{
        this.setState({record:data})
      })
    }

  //发表留言
    onSubmit = async ()=>{
      const {title,content} = this.state
      const messageinfo = {
        title,
        uid:storageUtils.getUser().id,
        mtime:this.getDate(new Date()),
        mid:storageUtils.getUser().id,
      }
      const messagedetail = {
        //id:storageUtils.getUser().id,
        mcontent:content
      }
      const obj = {
        "messageinfo" : messageinfo,
        "messagedetail" : messagedetail
      }
      //向后台发送数据
      if(!title||!content){
        message.error('标题和内容不能为空')
      }else{
          const result =await addComment(obj)
          console.log(obj)
        if(result.flag==='1'){
          message.success('发表成功')
          PubSub.publish("reload",'true')
          this.setState({title:'',content:'',contentCounts:200})
        }else{
          message.error('发表失败，请重新发表')
        }
      }
    }

    //发表评论
  onSubmit1 = async ()=>{
      const {record,comment} = this.state
      console.log("haha")
      console.log(record)
      if(comment){
        const messagedetail = {
          mcontent: comment,
          id:storageUtils.getUser().id,  //评论者id
          mid: record.id         //文章id
        }
        const result =await addComments(messagedetail)
        console.log(result)
        if(result.flag==='1'){
          message.success('评论成功')
          PubSub.publish("reload1",'true')
          this.setState({comment:'',commentCounts:100 })
        }else{
          message.error('评论失败，请重新评论')
        }
      }else{
        message.error('评论内容不能为空')
      }
  }
    //获取标题可写剩余字数
    counterTitle = (event)=> {
      let titleCounts = event.target.value + ""  //获取文本内容
      if (titleCounts.length > 10) {
        titleCounts = titleCounts.slice(0,10)
      }
      this.setState({title:titleCounts})
    }

    //获取文本框可写剩余字数
    counterContent = (event,num)=> {
      let contentCounts = event.target.value + "" //获取文本域的内容
      let count
      if (contentCounts.trim().length > num) {
        contentCounts = contentCounts.slice(0, num)
      } else {
        count = num - contentCounts.trim().length           //计算还能输入字数
      }
      if (num === 200)
        this.setState({contentCounts: count || 0, content: contentCounts})        //更新count
      else
        this.setState({commentCounts: count || 0, comment: contentCounts})        //更新count
    }


    render() {
        const {contentCounts,title,content,comment,commentCounts} =this.state
        const user = storageUtils.getUser()
        const display1 = storageUtils.getWrite1()
        const display2 = storageUtils.getWrite2()

        return (
          <div className='write'>
            <div className='write-head'>
              <p className='write-head-p font'>Hello  <span style={{fontSize:30,color:'#0074D9'}}>{user.userName}</span></p>
              <p className='out' onClick={this.out}>退出登录</p>
              <p className='write-head-p1 font'>欢迎登录BBS</p>
            </div>
            <div className='write-writting' style={{display:display1}}>
              <h2>发表新留言</h2>
              <div className='write-title'>
                <span className='font'>标题：</span>
                <Input className='write-title-input' placeholder='最多十位' onChange={this.counterTitle} value={title}/>
              </div>
              <div className='write-content'>
                <p className='font' style={{marginLeft:'-200px'}}>留言内容：</p>
                <TextArea
                  autosize={{ minRows: 10}}
                  className='write-content-input'
                  onChange={(event)=>this.counterContent(event,200)}
                  value={content}
                />
                <p className='tips'>还能输入{contentCounts}个字</p>
              </div>
              <br/>
              <div className='write-footer'>
                <Button type="primary" onClick={this.onSubmit} className="write-button">
                  发表
                </Button>
              </div>
            </div>
            <div className='write-writting1' style={{display:display2}}>
              <h2>添加新评论</h2>
              <div className='write-content'>
                <p className='font' style={{marginLeft:'-200px'}}>评论内容：</p>
                <TextArea
                  autosize={{ minRows: 6}}
                  className='write-content-input'
                  onChange={(event)=>this.counterContent(event,100)}
                  value={comment}
                />
                <p className='tips'>还能输入{commentCounts}个字</p>
                </div>
              <br/>
              <div className='write-footer'>
                <Button type="primary" onClick={this.onSubmit1} className="write-button">
                  发表
                </Button>
              </div>
            </div>
          </div>
        );
      }
}
