import React, {Component} from 'react';
import {Table} from 'antd';
import {message} from 'antd'
import PubSub from 'pubsub-js'

import {getTitles,delComment} from '../../../ajax/types'
import StorageUtils from '../../../utils/storageUtils'
import './titles.less'

export default class Title extends Component {

  state = {
    total:10,      //总数
    page:1,         //当前页数
    titles : [],    //存放标题
    loading:false,   //是否出现加载中图片
    desc:0,     //正逆序，默认正序
  }

  componentWillUnmount() {
    // 卸载异步操作设置状态
    clearTimeout(this.timeouter)
    this.setState = (state, callback) => {
      return
    }
  }

  //一开始挂起便请求标题，并修改state
  async componentDidMount() {
    StorageUtils.saveWrite1('block')
    StorageUtils.saveWrite2('none')
    let {page,desc} = this.state     //获取当前的页数
    this.reload = PubSub.subscribe('reload',async (msg,data)=>{   //每当用户发表的文章的时候及时更新
      if(data==='true'){
        this.setState({loading:true})     //开始请求出现load
        const result = await getTitles(page,10,desc)
        this.setState({total:result.total,titles:result.rows,loading:false})
      }
    })
    this.setState({loading:true})     //开始请求出现load
    let result = await getTitles(page,10,desc) //发送请求当前页面的标题
    this.setState({total:result.total,titles:result.rows,loading:false})  //请求完成取消load，以及改变state中的值
  }

  //删除留言
  deletemessage = async (mids)=>{
    const result = await delComment(mids+",")
    if(result.flag === '1'){
      message.success('删除成功')
      const {page,desc} = this.state
      this.setState({loading:true})     //开始请求出现load
      const result = await getTitles(page,10,desc)
      this.setState({total:result.total,titles:result.rows,loading:false})
    }else{
      message.error('删除失败')
    }
  }
  //获取明细
  getDetail = (record)=>{
    this.props.history.push('/home/detail',{record})
  }

  //获取页数
  getProducts = async (pageNum)=>{
    const {desc} = this.state
    this.pageNum = pageNum
    this.setState({page:pageNum})
    this.setState({loading:true})//开始请求出现load
    const result = await getTitles(pageNum,10,desc)
    this.setState({total:result.total,titles:result.rows,loading:false})//请求完成取消load，以及改变state中的值
  }


    render() {
      const {loading,total} = this.state

      const columns = [
        {
          title: '标题',
          dataIndex: 'title',
          key:"key",
          render:(text, record) => <a onClick={()=>this.getDetail(record)}>{text}</a>,//点击获取对应的id
          width:'40%',
          className:'Titles'
        },
        {
          title: '作者',
          dataIndex: 'ui.userName',
          key: 'userName',
          className:'Titles',
          width:'20%'
        },
        {
          title: '时间',
          dataIndex: 'mtime',
          key: 'date',
          width:'30%',
          className:'Titles'
        },
        {
          title: "",
          key: "action",
          width:'10%',
          height:'5%',
          className:'Titles',
          render: (text, record) => (
            record.uid !== StorageUtils.getUser().id?
              <a size="small" disabled >删除</a> :
              <a size="small" onClick={()=>this.deletemessage(record.id)}>删除</a>
          )
        }
      ]

      const titles = this.state.titles

      return (
            <div className='title'>
              <div className='title-div'>
                <p className='title-p'>留言列表</p>
              </div>
                <Table
                  rowKey='id'    //指定key为uid，可以自动设置为每个标题的key
                  loading={loading}  //是否出现加载标志
                  columns={columns}
                  dataSource={titles}
                  className='title-titles'
                  pagination={{
                    showQuickJumper: true, //快速跳转
                    total, //总数
                    onChange: this.getProducts ,//变化触发
                    current: this.pageNum //当前页
                  }}
                />
            </div>
        );
    }
}
