import React, {Component} from 'react'
import {Redirect} from 'react-router-dom'
import {
    Form,
    Icon,
    Input,
    Button,
    message
} from 'antd'

import {addNewUser,adminUser} from '../../ajax/types'
import storageUtils from '../../utils/storageUtils'
import User from '../../utils/users'

import './login.less'

const Item = Form.Item

class Login extends Component {

    state = {
        tohome:false
    }
    componentDidMount() {
        storageUtils.removeUser()
        storageUtils.removeWrite1()
        storageUtils.removeWrite2()
        User.user = {}
    }

    handleSubmit = (event) =>{
        event.preventDefault() //阻止表单的默认行为
        //收集表单数据并进行验证
        this.props.form.validateFields(async (err,values) =>{
            if(values.password!== values.password2){
                message.error("两次密码不一样，请重新输入密码")
            }else{
                if(!err){ //检验成功
                    const {userName,password} = values
                    const result = await addNewUser(userName,password)
                    console.log(result)
                    var result1
                    if(result.flag==='1')
                        result1 = await adminUser(userName,password)
                    console.log(result1)
                    if(result.flag==='1'&&result1.flag===1){
                        message.success('注册成功')
                        var user = result1.userinfo // 保存在内存中
                        User.user = user
                        storageUtils.saveUser(user) // 保存到local中
                        this.setState({tohome:true})
                    }else if(result.flag==='1'&&result1.flag!==1){
                        message.error("注册失败")
                    }
                }else{
                    message.error("数据格式错误，请重新填写")
                }
            }
        })

    }

    validatePwd = (rule, value, callback) => {
        if(!value) {
            callback('密码必须输入')
        } else if (value.length<4) {
            callback('密码长度不能小于4位')
        } else if (value.length>12) {
            callback('密码长度不能大于12位')
        } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
            callback('密码必须是英文、数字或下划线组成')
        } else {
            callback() // 验证通过
        }
    }

    render () {
        const form = this.props.form
        const { getFieldDecorator } = form;
        const {tohome} = this.state
        if(!User.user&&User.user.id){
            return <Redirect to='/'/>
        }
        if(tohome){
            return <Redirect to='/home'/>
        }
        return (
          <div className="login">
              <header className="login-header">
                  <h1>BBS</h1>
              </header>
              <section className="login-content">
                  <h2>用户注册</h2>
                  <Form onSubmit={this.handleSubmit} className="login-form">
                      <Item>
                          {
                              getFieldDecorator('userName', {
                                  rules: [
                                      { required: true, whitespace: true, message: '用户名必须输入' },
                                      { min: 4, message: '用户名至少4位' },
                                      { max: 12, message: '用户名最多12位' }
                                  ]
                              })(
                                <Input
                                  prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                  placeholder="用户名"
                                />
                              )
                          }
                      </Item>
                      <Item>
                          {
                              getFieldDecorator('password', {
                                  rules: [
                                      {
                                          validator: this.validatePwd
                                      }
                                  ]
                              })(
                                <Input
                                  prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                  type="password"
                                  placeholder="密码"
                                />
                              )
                          }
                      </Item>
                      <Item>
                          {
                              getFieldDecorator('password2', {

                              })(
                                <Input
                                  prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                  type="password"
                                  placeholder="再次确认密码"
                                />
                              )
                          }
                      </Item>
                      <Item>
                          <Button type="primary" htmlType="submit" className="login-form-button">
                              注册
                          </Button>
                      </Item>
                  </Form>
              </section>
          </div>
        )
    }
}

const WrapLogin = Form.create()(Login)
export default WrapLogin
