import store from 'store'
const USER_KEY = 'user_key'
const write1_KEY = 'write1_key'
const write2_KEY = 'write2_key'
export default {

  /*
    保存write状态
   */
  saveWrite1(write1){
    store.set(write1_KEY, write1)
  },
  saveWrite2(write2){
    store.set(write2_KEY, write2)
  },
  getWrite1(){
    return store.get(write1_KEY) || {}
  },
  getWrite2(){
    return store.get(write2_KEY) || {}
  },
  removeWrite1 () {
    store.remove(write1_KEY)
  },
  removeWrite2 () {
    store.remove(write2_KEY)
  },

  /*
  保存user
   */
  saveUser (user) {
    store.set(USER_KEY, user)
  },

  /*
  读取user
   */
  getUser () {
    return store.get(USER_KEY) || {}
  },

  /*
  删除user
   */
  removeUser () {
    store.remove(USER_KEY)
  }
}
